  transition: all 350ms cubic-bezier(0.48, 0.96, 0.57, 1.02); }

.screen-reader {
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }
  .screen-reader a:focus {
    position: relative;
    clip: auto;
    width: auto;
    height: auto;
    overflow: auto; }

.desktop {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  left: 0;
  top: 0; }

.cursor {
  top: 50%;
  left: 50%;
  position: absolute;
  width: 50px;
  height: 50px;
  margin: -25px 0 0 -25px;
  background: transparent;
  border: 1px solid #ffffff;
  border-radius: 80%;
  backface-visibility: hidden;
  visibility: hidden;
  z-index: 9999;
  filter: blur(0.8px);
  cursor: pointer;
  box-shadow: 0px 0px 10px 0px rgba(255, 255, 255, 0.35); }

